import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { Provider } from 'react-redux';  //  Provider
import store from './store'

ReactDOM.render(
  // store Providerstore
  <Provider store={store}> 
    <App />
  </Provider>,
  document.getElementById('root')
);
